import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import TimeField from 'react-simple-timefield';
import TimePicker from 'material-ui/TimePicker';


export default class TimePickers extends Component {
  constructor(props){
    super(props)
  }


  handleClick = (event, date) =>{

    const hours = date.getHours().length == 1 ? " " + date.getHours() : date.getHours();
    const minutes = date.getMinutes().length == 1 ? " " + date.getMinutes() : date.getMinutes();

    fetch("http://192.168.178.236/set/H=" + hours
    + "M=" + minutes, {
      method: "GET",
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      }
    })
    .then(() => {
      console.log("hoop")
    }) 
    .catch((error) => {
      console.log(error, "catch the hoop")
    })
  }

  styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
      width: 200,
    },
  });
    render(){
        return (
            <div>
          <br/>
          <TimePicker 
          format="24hr"
          hintText="Set Timer"
          onChange={this.handleClick}
          style={{
            width: '5em',
            marginBottom: '20px',
            marginLeft: '10px'
          }}
          />


            </div>
        );
    }
}

TimePickers.propTypes = {
  classes: PropTypes.object.isRequired,
};



