import React, { Component } from 'react';
import { Fetch } from 'react-request';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import Snackbar from '@material-ui/core/Snackbar';
import Fade from '@material-ui/core/Fade';


import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import TimePickers from './time-picker-component'
import AlertDialogSlide from './dialog-component'

import BackGround from './img/backgif.gif'

import {Card, CardActions, CardHeader, CardText} from 'material-ui/Card';

export default class MyComponent extends Component {

  constructor(props){
    super(props)
    this.state = {
      message: '',
      open:  true,
    }
  }

  handleClick = (event) =>{
    fetch("http://192.168.178.236/5/on", {
      method: "GET",
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      }
    })
    .then(() => {
      this.setState({
        message: "Gordijn open"
      })
    }) 
    .catch((error) => {
      console.log(error, "catch the hoop")
    })
  }
  handleClickSluit = (event) =>{
    fetch("http://192.168.178.236/5/off", {
      method: "GET",
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      }
    })
    .then(() => {
      this.setState({
        message: "Gordijn dicht"
      })
    }) 
    .catch((error) => {
      console.log(error, "catch the hoop")
    })
  }


  render() {
    return (
              <div className="container" style={{ 
                backgroundImage: BackGround
              }}>

              <br/>

            <Card>
                    <AppBar position="static" className="maincomponent">
                      <Tabs>
                        <Tab label="Connected with arduino" />
                      </Tabs>
                    </AppBar>
                    
                <Snackbar
                    TransitionComponent={Fade}
                    ContentProps={{
                      'aria-describedby': 'message-id',
                    }}
                    message={<span id="message-id">
                      {this.state.message}
                    </span>}
                    open = {this.state.open}
                  />

                <Button variant="raised" color="primary"
                onClick={this.handleClick}
                style={{
                  marginLeft: '10px',
                  marginBottom: '30px'
                }}
                >
                  Open gordijn
              </Button>
              <Button variant="raised" color="primary"
                onClick={this.handleClickSluit}
                style={{
                  marginLeft: '10px',
                  marginBottom: '30px'
                }}
                >
                  Sluit gordijn
              </Button>

              <br/>

              <TimePickers 
               />
         </Card>
              </div>
    );
  }
}


